import axios from 'axios'

/* –ü—Ä–µ–¥–≤–∞—Ä–∏—Ç–µ–ª—å–Ω—ã–π –∑–∞–ø—Ä–æ—Å –Ω–∞ –∑–∞–≥—Ä—É–∑–∫—É –≤—Å–µ—Ö –±–∏–¥–æ–≤ */
export const loadBids = () => async (dispatch, getState) => {

	await axios.get('/api/loadbid', getState)
		.then(res => {
			dispatch({
				type: 'BID_LOADED',
				payload: res.data
			})
		})
		.catch((err) => {
			dispatch({
				type: 'AUTH_ERROR'
			})
		})
}

/* –ó–∞–ø—Ä–∞—à–∏–≤–∞–µ–º —É —Å–µ—Ä–≤–µ—Ä–∞ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –æ –Ω–∞–ª–∏—á–∏–∏ –±–∏–¥–∞ —É –≤—ã–±—Ä–∞–Ω–Ω–æ–≥–æ –∏–≥—Ä–æ–∫–∞ */
export const loadCurrentBid = (playerId) => async (dispatch) => {

	await axios({
		url: '/api/loadcurrentbid',
		method: 'get',
		params: {bidId: playerId}
	})
	.then(res => {
		dispatch({
			type: 'BID_CURRENT_LOADED',
			payload: res.data
		})
	})
	.catch((err) => {
		dispatch({
			type: 'AUTH_ERROR'
		})
	})
}

/* –û—Ç–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –±–∏–¥–∞ –≤ –ë–î */
export const saveBid = (playerBid) => async (dispatch) => {

	await axios({
		url: '/api/bidsend',
		method: 'post',
		params: {bidId: playerBid.bidId},
		data: playerBid
	})
	.then((response) => {
		if (response.data === 'tooLateError')
			dispatch({
				type: 'BID_LATE_ERROR'
			})
		else
			dispatch({
				type: 'BID_LIST_UPDATE',
				payload: {bidData: response.data.value, isExisting: response.data.lastErrorObject.updatedExisting}
			})
			console.log(response)

		})
	.catch(() => {
			dispatch({
				type: 'BID_ERROR'
			})
	})
}

/* –û–±–Ω–æ–≤–ª–µ–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –≤ –ë–î –ø–æ—Å–ª–µ —Å–¥–µ–ª–∞–Ω–Ω–æ–≥–æ –±–∏–¥–∞  */
export const updateUser = (price, userId) => async (dispatch) => {

	await axios({
		url: '/api/changeuser',
		method: 'post',
		params: {userId: userId},
		data: {money: price}
	})
	.then((response) => {
		if (response)
			dispatch({
				type: 'USER_UPDATE',
				payload: {money: response.data}
			})
	})
	.catch(() => { console.log("Something wrong.") })	
}
